@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.project {
  width: 400px;
  height: 400px;
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 18px;
  background: $titleColor;
  border-radius: 8px;
  border: 1px solid #ccc;
  border-bottom: 6px solid #ccc;
  position: relative;
  @media screen and (max-width: 1090px) {
    margin-bottom: 20px;
  }


  .icon {
    width: 100%;
    height: 300px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: $bgColor;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.5;
    }

    .button {
      z-index: 1;
      @include button();
      @media screen and (max-width: 600px) {
        width: 100%;
      }
      opacity: 0.7;
    }
  }

  .projectInfo {
    .projectTitle {
      @include text(600, 0.9em, 1.2, $primaryColor);
    }

    .description {
      @include text(400, 0.8em, 1.2, $textColor);
      margin-top: 0;
    }
  }
}
